  let apps = [AbvCalc, TempCorrect, Strike, Convert, LMEdme,  FtoC];

  let choiceIn = AbvCalc;

  let appIndex = 0;


  function addApp(){
    apps.push(choiceIn);
    apps = apps;
    console.log(apps.length);
  }

  function removeApp(i){
    apps.splice(i, 1);
    console.log(apps.length);
    apps = apps;

  }
</script>

